import React from "react";
import './funnel.scss';
import {openVacancies} from "./data";

const data = openVacancies;

const OpenVacancy = () => {

    const cards = data.map( item => {

        const {id, title, step, nameStep} = item;

        return (
            <div key={id} className={"vacancy-list-card"} >
                <img className={"icon-arrow-left"} src={require("../../assets/funnel-image/arrow-left.png")} alt={""} />
                <div className={"vacancy-open-info"}>
                    <div className={"vacancy-name"}>{title}</div>
                    <div className={"vacancy-step"}>Step {step}: {nameStep}</div>
                </div>
            </div>
        )
    })

    return (
        <div>
            {cards}
        </div>
    )
}

export default OpenVacancy