import React from "react";
import './funnel.scss'
import OpenVacancy from "./vacancy-card-list";
import {openVacancies} from "./data";

const OpenVacancies = () => {

    //Поиск
    const vacancy_list = document.querySelector('.vacancy-list')

    const changeInput = (e) => {
        let search_request = e.target.value.toLowerCase();
        let arr = []

        openVacancies.forEach(item => {
            let item_name = item.title.toLowerCase()
            if (item_name.includes(search_request) === true) {
                arr.push(item)
            }
        });

        vacancy_list.innerHTML = ''

        if (arr.length === 0) {
            vacancy_list.innerHTML = `<p class="modal">Вакансии не найдены</p>`
        }

        arr.map( item => {
            const {id, title, step, nameStep} = item;

            return (
                <div key={id} className={"vacancy-list-card"} >
                    <img className={"icon-arrow-left"} src={require("../../assets/funnel-image/arrow-left.png")} alt={""} />
                    <div className={"vacancy-open-info"}>
                        <div className={"vacancy-name"}>{title}</div>
                        <div className={"vacancy-step"}>Step {step}: {nameStep}</div>
                    </div>
                </div>
            )
        })
    }

    return (
        <div className={"open-vacancies"}>
            <div className={"open-vacancies-title"}>
                <h2 className={"funnel-title"}>Open vacancy</h2>
                <div>
                    <input
                        onChange={changeInput}
                        placeholder={"Search"}
                        type={"text"}
                        id={"search-vacancy"}/>
                    <img src={require("../../assets/funnel-image/line-min.png")} alt={""}/>
                </div>
                <img id={"icon-lens"} src={require("../../assets/funnel-image/lupa.png")} alt={""}/>
            </div>
            <div className={"vacancy-list"}>
                <OpenVacancy />
            </div>
            <button id={"btn-new-vacancy"}>+ New vacancy</button>
        </div>
)
}

export default OpenVacancies