import React from "react";
import "../funnel/funnel.scss"
import "./dashboard.scss"
import { useDrag} from "react-dnd";
import {dashboardCards} from "./dataCard";

const StageCard = ({id, title, urlImage }) => {

        const [{isDragging}, drag] = useDrag(() => ({
            type: "image",
            item: { id:  id},
            collect: (monitor) => ({
                isDragging: !!monitor.isDragging(),
            }),
        }));

        return (
            <div className={"stage"} ref={drag}>
                <img className={"icon-stage"} src={require(`../../assets/dashboard/${urlImage}.png`)} alt={""}/>
                <p>{title}</p>
            </div>
        )
}

const cardList = dashboardCards.map(item => {
    const {id, title, urlImage} = item;
    return <StageCard id={id} urlImage={urlImage} title={title}/>
})

export  {StageCard, cardList}
